import { useState, useRef, useEffect, useCallback } from 'react';
import CopyButton from './CopyButton';
import SplitButton from './SplitButton';
import ChunkList from './ChunkLisе';

const TextAreaWithCounter = () => {
    const [text, setText] = useState('');
    const [splitTexts, setSplitTexts] = useState<string[]>([]);
    const textareaRef = useRef<HTMLTextAreaElement>(null);

    // Автоматическое изменение высоты основного textarea
    useEffect(() => {
        if (textareaRef.current) {
            textareaRef.current.style.height = 'auto';
            textareaRef.current.style.height = `${textareaRef.current.scrollHeight}px`;
        }
    }, [text]);

    const handleCopy = useCallback(() => {
        if (text) {
            navigator.clipboard.writeText(text);
        }
    }, [text]);

    const handleSplitText = useCallback(() => {
        if (!text) return;

        const chunks = [];
        for (let i = 0; i < text.length; i += 80) {
            chunks.push(text.slice(i, i + 80));
        }
        setSplitTexts(chunks);
    }, [text]);

    return (
        <div className="max-w-lg mx-auto p-4 bg-white rounded-lg shadow-md">
            <div className="relative">
                <textarea
                    ref={textareaRef}
                    value={text}
                    onChange={(e) => setText(e.target.value)}
                    className="w-full p-3 pr-20 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 resize-none overflow-hidden"
                    rows={1}
                    placeholder="Введите текст..."
                />
                <div className="absolute right-2 top-2 flex gap-1">
                    <SplitButton onClick={handleSplitText} disabled={!text} />
                    <CopyButton onClick={handleCopy} disabled={!text} />
                </div>
            </div>
            <div className="mt-1 text-sm text-gray-500">
                {text.length} символов
            </div>

            <ChunkList chunks={splitTexts} />
        </div>
    );
};

export default TextAreaWithCounter;